import {
    ICommandBarItemProps,
    classNamesFunction,
    List,
    IContextualMenuItem,
    Toggle,
    IconButton,
    Callout,
    FocusTrapZone,
    Calendar,
    DayOfWeek,
    ContextualMenuItemType,
} from "office-ui-fabric-react";
import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { trainingOverviewClassNames, ITrainingOverviewClassNames, getTrainingOverviewStyles } from "./TrainingOverview.styles";
import {
    TrainingHeader,
    TrainingAverage,
    TrainingMissing,
    TrainingMissingAverage,
    TrainingCount,
    TrainingAddMenuButton,
    TrainingAddButton,
    OnToggle,
    OffToggle,
    TrainingShowAllLabel,
    TrainingListHeader,
    TrainingDetails,
    DeleteButton,
    TrainingDateButton,
    DayPickerStrings,
    TrainingHeaderWithDate,
} from "../../../localization/DisplayStrings";
import {
    selectPushups,
    fromMonth,
    calcAverage,
    calcMissing,
    calcMissingAverage,
    calcTotal,
    addPushups,
    removePushups,
    ITrainingSummary,
    mapSummary,
    combineSummary,
} from "./TrainingOverviewSlice";
import { Page } from "../../../components/page/Page";
import { dateToString, dateToShortString, dateToMonthString } from "../../../utils/LocalizationUtil";

export function TrainingOverview(): JSX.Element {
    const classNames = classNamesFunction<{}, ITrainingOverviewClassNames>()(trainingOverviewClassNames);
    const styles = getTrainingOverviewStyles();

    const [refreshCount, setRefreshCount] = React.useState(0);
    const [total, setTotal] = React.useState(0);
    const [average, setAverage] = React.useState(0);
    const [missing, setMissing] = React.useState(0);
    const [missingAverage, setMissingAverage] = React.useState(0);
    const [showAll, setShowAll] = React.useState(false);
    const [currentDate, setCurrentDate] = React.useState(new Date());
    const [showDate, setShowDate] = React.useState(false);
    const [showCalendar, setShowCalendar] = React.useState(false);
    const [sortedPushups, setSortedPushups] = React.useState<ITrainingSummary[]>([]);

    const pushUps = useSelector(selectPushups);

    const dispatch = useDispatch();
    useEffect((): void => {
        const date = new Date();
        const fromThisMonth = fromMonth(pushUps, date);
        setTotal(calcTotal(fromThisMonth));
        setAverage(calcAverage(fromThisMonth, date));
        setMissing(calcMissing(fromThisMonth));
        setMissingAverage(calcMissingAverage(fromThisMonth, date));
        setSortedPushups(showAll ? mapSummary(pushUps) : combineSummary(pushUps));
    }, [pushUps, showAll]);

    const onShowAll = (event: React.MouseEvent<HTMLElement>, checked?: boolean) => {
        setRefreshCount(refreshCount + 1);
        setShowAll(!!checked);
    };
    const addItem = (count: number): IContextualMenuItem => {
        return {
            key: "add" + count,
            text: TrainingAddButton(count.toString()),
            secondaryText: dateToString(currentDate),
            onClick: (ev?: React.MouseEvent<HTMLElement> | React.KeyboardEvent<HTMLElement>, item?: IContextualMenuItem) => {
                dispatch(addPushups({ count, date: currentDate.toString() }));
            },
        };
    };

    const onSelectDate = (date: Date): void => {
        setShowCalendar(false);
        setShowDate(true);
        setCurrentDate(date);
    };

    const items: ICommandBarItemProps[] = [
        {
            key: "Add",
            text: TrainingAddMenuButton(),
            iconProps: {
                iconName: "ReportAdd",
            },
            ariaLabel: TrainingAddMenuButton(),
            subMenuProps: {
                items: [
                    {
                        itemType: ContextualMenuItemType.Header,
                        key: "addTitle",
                        text: dateToString(currentDate),
                    },
                    addItem(20),
                    addItem(25),
                    addItem(30),
                    addItem(35),
                    addItem(40),
                ],
            },
        },
        {
            key: "Date",
            text: TrainingDateButton(),
            iconProps: {
                iconName: "EventDate",
            },
            ariaLabel: TrainingDateButton(),
            onClick: () => {
                setShowCalendar(true);
            },
        },
    ];

    const removeClicked = (item: ITrainingSummary) => {
        if (item.data) {
            dispatch(removePushups(item.data));
        }
    };
    const onRenderCell = (item?: ITrainingSummary): JSX.Element | null => {
        if (!item) {
            return null;
        }

        if (item.isSummary) {
            return (
                <div className={classNames.listItem}>
                    <p className={classNames.listText}>{TrainingDetails(item.count.toString(), dateToMonthString(item.date))}</p>
                </div>
            );
        }

        return (
            <div className={classNames.listItem}>
                <p className={classNames.listText}>{TrainingDetails(item.count.toString(), dateToString(item.date))}</p>
                <IconButton
                    iconProps={{ iconName: "Delete" }}
                    title={DeleteButton()}
                    ariaLabel={DeleteButton()}
                    onClick={() => {
                        removeClicked(item);
                    }}
                    styles={styles.listButton}
                />
            </div>
        );
    };

    const header = showDate ? TrainingHeaderWithDate(dateToShortString(currentDate)) : TrainingHeader();
    return (
        <Page commandBarItems={items} header={header}>
            <p>{TrainingCount(total.toFixed(0))}</p>
            <p>{TrainingAverage(average.toFixed(0))}</p>
            <p>{TrainingMissing(missing.toFixed(0))}</p>
            <p>{TrainingMissingAverage(missingAverage.toFixed(0))}</p>
            <h2>{TrainingListHeader()}</h2>
            <Toggle label={TrainingShowAllLabel()} checked={showAll} inlineLabel onText={OnToggle()} offText={OffToggle()} onChange={onShowAll} />
            <List className={classNames.list} version={refreshCount} items={sortedPushups} onRenderCell={onRenderCell} />
            {showCalendar && (
                <Callout
                    isBeakVisible={false}
                    className="ms-DatePicker-callout"
                    gapSpace={0}
                    doNotLayer={false}
                    onDismiss={() => {
                        setShowCalendar(false);
                    }}
                    setInitialFocus
                >
                    <FocusTrapZone firstFocusableSelector="ms-DatePicker-day--today" isClickableOutsideFocusTrap>
                        <Calendar
                            onSelectDate={onSelectDate}
                            onDismiss={() => {
                                setShowCalendar(false);
                            }}
                            isMonthPickerVisible={false}
                            value={currentDate!}
                            firstDayOfWeek={DayOfWeek.Monday}
                            strings={DayPickerStrings()}
                            isDayPickerVisible={true}
                            highlightCurrentMonth={true}
                            highlightSelectedMonth={true}
                            showMonthPickerAsOverlay={false}
                            showGoToToday={true}
                        />
                    </FocusTrapZone>
                </Callout>
            )}
        </Page>
    );
}
