import { IButtonStyles, memoizeFunction, mergeStyleSets, IChoiceGroupOptionStyles } from "office-ui-fabric-react";

export interface IScorePanelStyles {
    footer: Partial<IButtonStyles>;
    choiceGroup: Partial<IChoiceGroupOptionStyles>;
}

export const getScorePanelStyles = (): IScorePanelStyles => ({
    footer: {
        root: { margin: "5px" },
    },
    choiceGroup: {
        choiceFieldWrapper: {
            backgroundColor: "white",
        },
    },
});

export interface IScorePanelClassNames {
    wrapper: string;
}

export const scorePanelClassNames = memoizeFunction(
    (): IScorePanelClassNames => {
        return mergeStyleSets({
            wrapper: {
                display: "flex",
                alignItems: "flex-end",
                margin: "30px 5px",
            },
        });
    }
);
