import { ComboBox, IComboBoxOption, IComboBox, PrimaryButton, DefaultButton, Dialog, DialogType, DialogFooter } from "office-ui-fabric-react";
import React, { useEffect } from "react";

import { map } from "lodash";
import { IMahJongUser, createUser } from "../Mahjong.model";
import { OkButton, CancelButton, AddUserTitle } from "../../../localization/DisplayStrings";
import { useDispatch } from "react-redux";
import { addUser } from "../overview/MahjongOverviewSlice";

export interface AddUserPanelProps {
    isOpen: boolean;
    onAdd: (player: IMahJongUser) => void;
    onCancel: () => void;
    allPlayers: IMahJongUser[];
}

export function AddUserPanel(props: AddUserPanelProps): JSX.Element {
    const { isOpen, onAdd, onCancel, allPlayers } = props;
    const [newPlayer, setNewPlayer] = React.useState<IMahJongUser | undefined>();
    const [options, setOptions] = React.useState<IComboBoxOption[]>([]);

    const dispatch = useDispatch();
    useEffect((): void => {
        setNewPlayer(undefined);
    }, [isOpen]);

    const mapUserToOption = (user: IMahJongUser): IComboBoxOption => {
        return { key: user.id, text: user.name, data: user };
    };

    useEffect((): void => {
        setOptions(map(allPlayers, mapUserToOption));
    }, [allPlayers]);

    const onSaveClicked = () => {
        if (newPlayer) {
            onAdd(newPlayer);
        }
    };

    const onAddUserChange = (event: React.FormEvent<IComboBox>, option?: IComboBoxOption, index?: number, value?: string) => {
        if (option?.data) {
            setNewPlayer(option.data);
        } else {
            const text: string | undefined = option?.text || value;
            if (text) {
                const createdPlayer = createUser(text);
                dispatch(addUser(createdPlayer));
                setNewPlayer(createdPlayer);
                setOptions([...options, mapUserToOption(createdPlayer)]);
            }
        }
    };

    return (
        <Dialog
            hidden={!isOpen}
            onDismiss={onCancel}
            dialogContentProps={{
                type: DialogType.normal,
                title: AddUserTitle(),
                closeButtonAriaLabel: CancelButton(),
            }}
            modalProps={{
                isBlocking: false,
                // styles: { main: { maxWidth: 450 } },
            }}
        >
            <ComboBox
                ariaLabel={AddUserTitle()}
                key="User"
                selectedKey={newPlayer?.id}
                allowFreeform={true}
                autoComplete="on"
                onChange={onAddUserChange}
                options={options}
            />
            <DialogFooter>
                <PrimaryButton onClick={onSaveClicked} text={OkButton()} />
                <DefaultButton onClick={onCancel} text={CancelButton()} />
            </DialogFooter>
        </Dialog>
    );
}
