import { ComboBox, IComboBoxOption, IComboBox, Panel, PrimaryButton, IChoiceGroupOption, ChoiceGroup, Stack, Toggle } from "office-ui-fabric-react";
import React, { useEffect } from "react";

import { getScorePanelStyles } from "./ScorePanel.styles";
import { IMahJongSet, MahJongSetTypes, MahJongTileTypes } from "../Mahjong.model";
import {
    CloseButton,
    OkButton,
    EditScoreTitle,
    ChooseTileTypeLabel,
    OnToggle,
    OffToggle,
    InHandLabel,
    OneOrNineLabel,
    OwnWindLabel,
    ChooseSetTypeLabel,
} from "../../../localization/DisplayStrings";
import { displayTileType, displaySetType, imageTileType } from "../MahjongDisplay";

export interface ScorePanelProps {
    isOpen: boolean;
    onClose: (set: IMahJongSet) => void;
    onCancel: () => void;
}

export function ScorePanel(props: ScorePanelProps): JSX.Element {
    const styles = getScorePanelStyles();

    const { isOpen, onClose, onCancel } = props;
    const [setType, setSetType] = React.useState<MahJongSetTypes>(MahJongSetTypes.Poong);
    const [tileType, setTileType] = React.useState<MahJongTileTypes>(MahJongTileTypes.Bamboo);
    const [inHand, setInHand] = React.useState(false);
    const [oneOrNine, setOneOrNine] = React.useState(false);
    const [ownWind, setOwnWind] = React.useState(false);

    useEffect((): void => {
        setSetType(MahJongSetTypes.Poong);
        setTileType(MahJongTileTypes.Bamboo);
        setInHand(false);
        setOneOrNine(false);
        setOwnWind(false);
    }, [isOpen]);

    const onSaveClicked = () => {
        onClose({
            tileType,
            setType,
            inHand,
            oneOrNine,
            ownWind,
        });
    };
    const onChangeInHand = (event: React.MouseEvent<HTMLElement>, checked?: boolean) => {
        setInHand(!!checked);
    };
    const onChangeOneOrNine = (event: React.MouseEvent<HTMLElement>, checked?: boolean) => {
        setOneOrNine(!!checked);
    };
    const onChangeOwnWind = (event: React.MouseEvent<HTMLElement>, checked?: boolean) => {
        setOwnWind(!!checked);
    };
    const onChangeTileType = (ev?: React.FormEvent<HTMLElement | HTMLInputElement>, option?: IChoiceGroupOption) => {
        const tileType = parseInt(option?.key ?? MahJongTileTypes.Bamboo.toString()) as MahJongTileTypes;
        if (tileType === MahJongTileTypes.Flower) {
            if (setType === MahJongSetTypes.Poong) {
                setSetType(MahJongSetTypes.Single);
            }
        } else if (setType === MahJongSetTypes.Single) {
            setSetType(MahJongSetTypes.Poong);
        }
        setTileType(tileType);
    };
    const onChangeSetType = (event: React.FormEvent<IComboBox>, option?: IComboBoxOption) => {
        setSetType((option?.key ?? MahJongSetTypes.Poong) as MahJongSetTypes);
    };

    const getTileOption = (key: MahJongTileTypes, disabled?: boolean): IChoiceGroupOption => {
        return {
            key: key.toString(),
            imageSrc: imageTileType(key),
            selectedImageSrc: imageTileType(key),
            imageSize: { width: 40, height: 55 },
            imageAlt: displayTileType(key),
            text: displayTileType(key),
            disabled,
            styles: styles.choiceGroup,
        };
    };

    const getTileOptions = (): IChoiceGroupOption[] => {
        // const isSingle = setType === MahJongSetTypes.Single;
        const isChow = setType === MahJongSetTypes.Chow;
        return [
            getTileOption(MahJongTileTypes.Dragon, isChow),
            getTileOption(MahJongTileTypes.Wind, isChow),
            getTileOption(MahJongTileTypes.Character),
            getTileOption(MahJongTileTypes.Circle),
            getTileOption(MahJongTileTypes.Bamboo),
            getTileOption(MahJongTileTypes.Flower, isChow),
        ];
    };

    const getSetOption = (key: MahJongSetTypes): IComboBoxOption => {
        return { key, text: displaySetType(key) };
    };

    const getSetOptions = (): IComboBoxOption[] => {
        const options = [];
        if (tileType === MahJongTileTypes.Bamboo || tileType === MahJongTileTypes.Circle || tileType === MahJongTileTypes.Character) {
            options.push(getSetOption(MahJongSetTypes.Chow));
        }

        options.push(getSetOption(MahJongSetTypes.Poong));
        options.push(getSetOption(MahJongSetTypes.Kong));
        options.push(getSetOption(MahJongSetTypes.Pair));

        if (tileType === MahJongTileTypes.Flower) {
            options.push(getSetOption(MahJongSetTypes.Single));
        }

        return options;
    };

    // <DefaultButton styles={styles.footer} onClick={onCancel}>{CancelButton()}</DefaultButton>;
    const onRenderFooterContent = () => (
        <div>
            <PrimaryButton styles={styles.footer} onClick={onSaveClicked}>
                {OkButton()}
            </PrimaryButton>
        </div>
    );

    const isInHandVisible = tileType !== MahJongTileTypes.Flower;
    const isOneOrNineVisible =
        tileType === MahJongTileTypes.Bamboo || tileType === MahJongTileTypes.Circle || tileType === MahJongTileTypes.Character;
    const isOwnWindVisible = tileType === MahJongTileTypes.Wind;

    return (
        <div>
            <Panel
                isOpen={isOpen}
                isLightDismiss
                onDismiss={onCancel}
                headerText={EditScoreTitle()}
                closeButtonAriaLabel={CloseButton()}
                onRenderFooterContent={onRenderFooterContent}
                isFooterAtBottom={true}
            >
                <Stack tokens={{ childrenGap: 10 }}>
                    <ChoiceGroup
                        label={ChooseTileTypeLabel()}
                        selectedKey={tileType.toString()}
                        onChange={onChangeTileType}
                        options={getTileOptions()}
                    />
                    <ComboBox
                        label={ChooseSetTypeLabel()}
                        aria-label=""
                        autoComplete="on"
                        selectedKey={setType}
                        options={getSetOptions()}
                        onChange={onChangeSetType}
                    />
                    {isInHandVisible && (
                        <Toggle
                            label={InHandLabel()}
                            checked={inHand}
                            inlineLabel
                            onText={OnToggle()}
                            offText={OffToggle()}
                            onChange={onChangeInHand}
                        />
                    )}
                    {isOneOrNineVisible && (
                        <Toggle
                            label={OneOrNineLabel()}
                            checked={oneOrNine}
                            inlineLabel
                            onText={OnToggle()}
                            offText={OffToggle()}
                            onChange={onChangeOneOrNine}
                        />
                    )}
                    {isOwnWindVisible && (
                        <Toggle
                            label={OwnWindLabel()}
                            checked={ownWind}
                            inlineLabel
                            onText={OnToggle()}
                            offText={OffToggle()}
                            onChange={onChangeOwnWind}
                        />
                    )}
                </Stack>
            </Panel>
        </div>
    );
}
