import { ICommandBarStyles, IStackStyles, memoizeFunction, mergeStyleSets, IButtonStyles } from "office-ui-fabric-react";

export interface IMahjongDetailsStyles {
    commandBar: ICommandBarStyles;
    dashboard: IStackStyles;
    listButton: Partial<IButtonStyles>;
    footer: Partial<IButtonStyles>;
}

export const getMahjongDetailsStyles = (): IMahjongDetailsStyles => ({
    commandBar: {
        root: {
            padding: "5px",
        },
    },
    dashboard: {
        root: { padding: "24px 0 0 0" },
    },
    listButton: {
        root: {
            marginTop: "10px",
            marginLeft: "auto",
        },
    },
    footer: {
        root: {
            marginTop: 30,
            float: "right",
            marginRight: 30,
        },
    },
});

export interface IMahjongDetailsClassNames {
    page: string;
    content: string;
    listItem: string;
    listDescription: string;
}

export const mahjongDetailsClassNames = memoizeFunction(
    (): IMahjongDetailsClassNames => {
        return mergeStyleSets({
            page: {
                overflow: "auto",
                // background: '#F8F8F9'
            },
            content: {
                margin: "10px",
            },
            listItem: {
                display: "flex",
                margin: "10px",
            },
            listDescription: {
                margin: "0 20px",
                padding: "5px",
            },
        });
    }
);
