import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { AppThunk, RootState } from "../../../app/store";
import { isSupported, loadData, saveData } from "../../../utils/StorageUtil";
import { find, filter, without, sumBy, minBy, max, sortBy, chain, groupBy, forEach } from "lodash";

export interface IPushup {
    count: number;
    date?: string;
}

export interface ITrainingSummary {
    count: number;
    date: Date;
    isSummary: boolean;
    data?: IPushup;
}

interface TrainingOverviewState {
    pushups: IPushup[];
}

const initialState: TrainingOverviewState = {
    pushups: [],
};

export const perMonth = 500;

export const overviewSlice = createSlice({
    name: "TrainingOverview",
    initialState,
    reducers: {
        loadFailed: (state) => {
            state = initialState;
        },
        loadSuccess: (state, action: PayloadAction<TrainingOverviewState>) => {
            state.pushups = action.payload?.pushups ?? [];
        },
        addPushups: (state, action: PayloadAction<IPushup>) => {
            let today = fromDate(state.pushups, new Date(action.payload.date ?? new Date()));
            state.pushups = without(state.pushups, ...today);

            const newSet = {
                count: action.payload.count,
                date: (action.payload.date ?? new Date()).toString(),
            };
            today.push(newSet);
            while (today.length > 2) {
                const minCount = minBy(today, (pushup: IPushup): number => pushup.count) || newSet;
                today = without<IPushup>(today, minCount);
            }
            state.pushups.push(...today);
            saveState(state);
        },
        removePushups: (state, action: PayloadAction<IPushup>) => {
            const found = find(
                state.pushups,
                (pushUp: IPushup): boolean => pushUp.count === action.payload.count && pushUp.date === action.payload.date
            );
            if (found) {
                state.pushups = without(state.pushups, found);
            }

            saveState(state);
        },
    },
});

const { loadFailed, loadSuccess } = overviewSlice.actions;
export const { addPushups, removePushups } = overviewSlice.actions;

const storageName = "Training";

function saveState(state: TrainingOverviewState) {
    saveData(storageName, state);
}

export const load = (): AppThunk => (dispatch) => {
    if (isSupported) {
        dispatch(loadSuccess(loadData(storageName) as TrainingOverviewState));
    } else {
        dispatch(loadFailed());
    }
};

function fromDate(pushups: IPushup[], date: Date) {
    return filter(
        pushups,
        (pushup: IPushup): boolean =>
            new Date(pushup.date ?? new Date()).getDate() === date.getDate() &&
            new Date(pushup.date ?? new Date()).getMonth() === date.getMonth() &&
            new Date(pushup.date ?? new Date()).getFullYear() === date.getFullYear()
    );
}

export function fromMonth(pushups: IPushup[], current: Date = new Date()) {
    return filter(
        pushups,
        (pushup: IPushup): boolean =>
            new Date(pushup.date ?? new Date()).getMonth() === current.getMonth() &&
            new Date(pushup.date ?? new Date()).getFullYear() === current.getFullYear()
    );
}

export function calcAverage(pushups: IPushup[], current: Date) {
    const sum = sumBy(pushups, (pushup: IPushup): number => pushup.count);
    return sum / current.getDate();
}

export function calcTotal(pushups: IPushup[]) {
    return sumBy(pushups, (pushup: IPushup): number => pushup.count) ?? 0;
}

export function calcMissing(pushups: IPushup[]) {
    return max<number>([perMonth - sumBy(pushups, (pushup: IPushup): number => pushup.count), 0]) ?? 0;
}

export function calcMissingAverage(pushups: IPushup[], current: Date) {
    const count = calcMissing(pushups);
    return count / (daysInMonth(current) - current.getDate() + 1);
}

function daysInMonth(current: Date): number {
    return new Date(current.getFullYear(), current.getMonth() + 1, 0).getDate();
}

function summaryDate(current: Date): Date {
    return new Date(current.getFullYear(), current.getMonth(), 1);
}

export function combineSummary(pushups: IPushup[]): ITrainingSummary[] {
    const sorted = sortBy(pushups, (pushUp): Date => new Date(pushUp.date ?? new Date())).reverse();
    const thisMonth = summaryDate(new Date());
    const result: ITrainingSummary[] = [];
    forEach(sorted, (pushup) => {
        const actualDate = new Date(pushup.date ?? new Date());
        const sumDate = summaryDate(actualDate);
        if (sumDate.toString() === thisMonth.toString()) {
            result.push({ date: actualDate, count: pushup.count, isSummary: false, data: pushup });
        } else {
            let found = find(result, (sum) => sum.date.toString() === sumDate.toString());
            if (!found) {
                found = { date: sumDate, count: 0, isSummary: true };
                result.push(found);
            }

            found.count += pushup.count;
        }
    });

    return result;
}

export function mapSummary(pushups: IPushup[]): ITrainingSummary[] {
    return sortBy(pushups, (pushUp): Date => new Date(pushUp.date ?? new Date()))
        .reverse()
        .map((pushUp) => ({ date: new Date(pushUp.date ?? new Date()), count: pushUp.count, isSummary: false, data: pushUp }));
}

export const selectPushups = (state: RootState) => state.training.pushups;

export default overviewSlice.reducer;
