import { ICommandBarStyles, IStackStyles, memoizeFunction, mergeStyleSets, IButtonStyles } from "office-ui-fabric-react";

export interface IPlaygroundOverviewStyles {
    commandBar: ICommandBarStyles;
    dashboard: IStackStyles;
    listItem: Partial<IButtonStyles>;
    footerButton: Partial<IButtonStyles>;
}

export const getPlaygroundOverviewStyles = (): IPlaygroundOverviewStyles => ({
    commandBar: {
        root: {
            padding: "5px",
        },
    },
    dashboard: {
        root: { padding: "24px 0 0 0" },
    },
    listItem: {
        root: {
            maxWidth: "100%",
            width: "100%",
            padding: "20px 10px 20px 20px",
            margin: "10px 0",
        },
        flexContainer: {
            justifyContent: "flex-start",
        },
    },
    footerButton: {
        root: {
            marginTop: 30,
            float: "right",
            marginRight: 30,
        },
    },
});

export interface IPlaygroundOverviewClassNames {
    page: string;
    list: string;
    header: string;
    winnerIcon: string;
    winnerIcon2: string;
    eastIcon: string;
}

export const playgroundOverviewClassNames = memoizeFunction(
    (): IPlaygroundOverviewClassNames => {
        return mergeStyleSets({
            page: {
                overflow: "auto",
                margin: "0 10px",
            },
            list: {
                margin: "0 10px",
            },
            header: {
                marginLeft: "10px",
                marginRight: "10px",
            },
            winnerIcon: {
                fontSize: 20,
                height: 20,
                width: 20,
                marginRight: "10px",
                marginLeft: "auto",
                color: "green",
            },
            winnerIcon2: {
                fontSize: 20,
                height: 20,
                width: 20,
                marginRight: "10px",
                marginLeft: "0px",
                color: "green",
            },
            eastIcon: {
                fontSize: 20,
                height: 20,
                width: 20,
                marginRight: "10px",
                marginLeft: "auto",
            },
        });
    }
);
