import { configureStore, ThunkAction, Action } from "@reduxjs/toolkit";
import mainReducer from "../pages/main/MainSlice";
import overviewReducer from "../pages/mahjong/overview/MahjongOverviewSlice";
import trainingReducer from "../pages/training/overview/TrainingOverviewSlice";
import playgroundReducer from "../pages/playground/overview/PlaygroundOverviewSlice";

export const store = configureStore({
  reducer: {
    main: mainReducer,
    mahjongOverview: overviewReducer,
    training: trainingReducer,
    playground: playgroundReducer,
  },
});

export type RootState = ReturnType<typeof store.getState>;
export type AppThunk<ReturnType = void> = ThunkAction<
  ReturnType,
  RootState,
  unknown,
  Action<string>
>;
