import { CommandBar, ICommandBarItemProps, classNamesFunction } from "office-ui-fabric-react";
import React, { useEffect } from "react";
import { useDispatch } from "react-redux";

import { pageClassNames, getPageStyles, IPageClassNames } from "./Page.styles";
import { HomeButton } from "../../localization/DisplayStrings";
import { navigate, Page as PageType } from "../../pages/main/MainSlice";

export interface IPageProps extends JSX.ElementChildrenAttribute {
    commandBarItems: ICommandBarItemProps[];
    hideHome?: boolean;
    header: string | JSX.Element;
}

export function Page({ commandBarItems, hideHome, header, children }: IPageProps): JSX.Element {
    const classNames = classNamesFunction<{}, IPageClassNames>()(pageClassNames);
    const styles = getPageStyles();

    const dispatch = useDispatch();
    const [items, setItems] = React.useState<ICommandBarItemProps[]>([]);
    useEffect((): void => {
        setItems([
            {
                key: "Back",
                // disabled: hideHome,
                ariaLabel: HomeButton(),
                iconProps: {
                    iconName: "Home",
                },
                onClick: () => {
                    dispatch(navigate(hideHome ? PageType.Training : PageType.Main));
                },
            },
            ...commandBarItems,
        ]);
    }, [commandBarItems, dispatch, hideHome]);

    return (
        <div className={classNames.page}>
            <div className={classNames.commandBar}>
                <CommandBar items={items} styles={styles.commandBar} />
            </div>
            <div className={classNames.content}>
                {<h1>{header}</h1>}
                {children}
            </div>
        </div>
    );
}
