import { IButtonStyles, memoizeFunction, mergeStyleSets, IComboBoxStyles, ITextFieldStyles } from "office-ui-fabric-react";

export interface ISelectUserPanelStyles {
    combobox: Partial<IComboBoxStyles>;
    footer: Partial<IButtonStyles>;
    firstRoot: Partial<ITextFieldStyles>;
    firstContainer: Partial<IComboBoxStyles>;
}

export const getSelectUserPanelStyles = (): ISelectUserPanelStyles => ({
    footer: {
        root: { margin: "5px" },
    },
    combobox: {
        root: { margin: "5px" },
    },
    firstRoot: {
        root: {
            flexGrow: "1",
            flexShrink: "1",
        },
    },
    firstContainer: {
        container: {
            flexGrow: "1",
            flexShrink: "1",
        },
    },
});

export interface ISelectUserPanelClassNames {
    wrapper: string;
}

export const selectUserPanelClassNames = memoizeFunction(
    (): ISelectUserPanelClassNames => {
        return mergeStyleSets({
            wrapper: {
                display: "flex",
                alignItems: "flex-end",
                margin: "30px 5px",
            },
        });
    }
);
