import { classNamesFunction, ComboBox, IComboBoxOption, IComboBox, Panel, PrimaryButton, IconButton } from "office-ui-fabric-react";
import React, { useEffect } from "react";

import { selectUserPanelClassNames, getSelectUserPanelStyles, ISelectUserPanelClassNames } from "./SelectUserPanel.styles";
import { map, find, filter, isEmpty } from "lodash";
import { IMahJongUser, createUser } from "../Mahjong.model";
import { EditPlayersTitle, CloseButton, DeleteButton, DeletePlayerLabel, OkButton } from "../../../localization/DisplayStrings";

export interface SelectUserPanelProps {
    isOpen: boolean;
    onSave: (selectedPlayers: IMahJongUser[]) => void;
    onDelete: (player: IMahJongUser) => void;
    allPlayers: IMahJongUser[];
    currentPlayers: IMahJongUser[];
}

export function SelectUserPanel(props: SelectUserPanelProps): JSX.Element {
    const classNames = classNamesFunction<{}, ISelectUserPanelClassNames>()(selectUserPanelClassNames);
    const styles = getSelectUserPanelStyles();

    const { isOpen, onSave, onDelete, allPlayers, currentPlayers } = props;
    const [selectedPlayers, setSelectedPlayers] = React.useState<IMahJongUser[]>([]);
    const [userToDelete, setUserToDelete] = React.useState<IMahJongUser | null>(null);

    useEffect((): void => {
        const selected = [...currentPlayers];
        let i = selected.length;
        for (i; i < 4; i++) {
            selected.push(createUser(""));
        }
        setSelectedPlayers(selected);
    }, [currentPlayers]);

    const onChange = (selectedIndex: number) => (event: React.FormEvent<IComboBox>, option?: IComboBoxOption) => {
        const selected = [...selectedPlayers];
        if (selected.length > selectedIndex) {
            selected[selectedIndex] = option?.data || createUser("");
        }
        setSelectedPlayers(selected);
    };

    const onSaveClicked = () => {
        onSave(filter(selectedPlayers, (user: IMahJongUser): boolean => !isEmpty(user.name)));
    };

    const options = [
        { key: "_", text: "" },
        ...map(
            allPlayers,
            (user: IMahJongUser): IComboBoxOption => {
                return { key: user.id, text: user.name, data: user };
            }
        ),
    ];

    const filteredOptions = (user: IMahJongUser): IComboBoxOption[] => {
        const filtered = filter(options, (option: IComboBoxOption): boolean => {
            return option.key === user.id || !find(selectedPlayers, (selected: IMahJongUser): boolean => option.key === selected.id);
        });
        return filtered.length === 1 ? [] : filtered;
    };

    const renderComboBox = (user: IMahJongUser, key: number): JSX.Element => (
        <ComboBox
            key={key}
            aria-label=""
            selectedKey={user.id}
            autoComplete="on"
            options={filteredOptions(user)}
            onChange={onChange(key)}
            styles={styles.combobox}
        />
    );

    const allComboboxes = map(selectedPlayers, (user: IMahJongUser, index: number): JSX.Element => renderComboBox(user, index));

    const onDeleteUserChange = (event: React.FormEvent<IComboBox>, option?: IComboBoxOption) => {
        setUserToDelete(option?.data);
    };
    const onDeleteUserClicked = () => {
        if (userToDelete) {
            onDelete(userToDelete);
        }
    };

    const renderDeleteCombobox = (): JSX.Element => {
        return (
            <div className={classNames.wrapper}>
                <ComboBox
                    label={DeletePlayerLabel()}
                    aria-label=""
                    autoComplete="on"
                    selectedKey={userToDelete?.id}
                    options={options.length === 1 ? [] : options}
                    onChange={onDeleteUserChange}
                    styles={styles.firstContainer}
                />
                <IconButton
                    iconProps={{ iconName: "Delete" }}
                    onClick={onDeleteUserClicked}
                    title={DeleteButton()}
                    ariaLabel={DeleteButton()}
                    disabled={isEmpty(userToDelete?.id)}
                />
            </div>
        );
    };

    const onRenderFooterContent = () => (
        <div>
            <PrimaryButton styles={styles.footer} onClick={onSaveClicked}>
                {OkButton()}
            </PrimaryButton>
        </div>
    );

    return (
        <div>
            <Panel
                isOpen={isOpen}
                isLightDismiss
                onDismiss={onSaveClicked}
                headerText={EditPlayersTitle()}
                closeButtonAriaLabel={CloseButton()}
                onRenderFooterContent={onRenderFooterContent}
                isFooterAtBottom={true}
            >
                {allComboboxes}
                {renderDeleteCombobox()}
            </Panel>
        </div>
    );
}
