import { CommandBar, ICommandBarItemProps, classNamesFunction, Toggle, Stack, List, Image, IconButton, PrimaryButton } from "office-ui-fabric-react";
import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";

import { mahjongDetailsClassNames, getMahjongDetailsStyles, IMahjongDetailsClassNames } from "./MahjongDetails.styles";
import {
    BackButton,
    EastToggleLabel,
    WinnerToggleLabel,
    OnToggle,
    OffToggle,
    CurrentScore,
    AddButton,
    DeleteButton,
} from "../../../localization/DisplayStrings";
import { updateScore, selectCurrentGame, scoreToPoints } from "../overview/MahjongOverviewSlice";
import { IMahJongUser, IMahJongScore, getScore, createScore, IMahJongSet } from "../Mahjong.model";
import { ScorePanel } from "../scorePanel/ScorePanel";
import { displayTileType, displaySetType, imageSet } from "../MahjongDisplay";
import { map, without } from "lodash";

export interface MahjongDetailsProps {
    user: IMahJongUser;
    onNavigateBack: () => void;
}

export function MahjongDetails(props: MahjongDetailsProps): JSX.Element {
    const classNames = classNamesFunction<{}, IMahjongDetailsClassNames>()(mahjongDetailsClassNames);
    const styles = getMahjongDetailsStyles();

    const { user, onNavigateBack } = props;

    const currentGame = useSelector(selectCurrentGame);
    const [userScore, setUserScore] = React.useState<IMahJongScore | undefined>(undefined);
    const [showScorePanel, setShowScorePanel] = React.useState(false);

    useEffect((): void => {
        if (user && currentGame) {
            setUserScore(getScore(currentGame, user));
        }
    }, [user, currentGame]);

    const initialScore = createScore("");
    const setWinner = (isWinner?: boolean) => {
        const tempScore = {
            ...initialScore,
            ...userScore,
            isWinner,
        };
        tempScore.points = scoreToPoints(tempScore);
        setUserScore(tempScore);
    };
    const setEast = (isEast?: boolean) => {
        setUserScore({ ...initialScore, ...userScore, isEast });
    };

    const dispatch = useDispatch();
    const closeDetails = () => {
        if (userScore) {
            dispatch(updateScore(userScore));
        }
        onNavigateBack();
    };
    const closeScorePanel = (set: IMahJongSet) => {
        if (userScore) {
            const tempScore = {
                ...initialScore,
                ...userScore,
                sets: [...(userScore.sets || []), set],
            };
            tempScore.points = scoreToPoints(tempScore);
            setUserScore(tempScore);
        }
        setShowScorePanel(false);
    };
    const removeSetClicked = (set: IMahJongSet) => {
        if (userScore) {
            const tempScore = {
                ...initialScore,
                ...userScore,
                sets: without(userScore.sets, set),
            };
            tempScore.points = scoreToPoints(tempScore);
            setUserScore(tempScore);
        }
    };
    const cancelScorePanel = () => setShowScorePanel(false);
    const onshowScorePanel = () => {
        setShowScorePanel(true);
    };
    const onChangeWinner = (event: React.MouseEvent<HTMLElement>, checked?: boolean) => setWinner(checked);
    const onChangeEast = (event: React.MouseEvent<HTMLElement>, checked?: boolean) => setEast(checked);

    const items: ICommandBarItemProps[] = [
        {
            key: "Back",
            name: BackButton(),
            iconProps: {
                iconName: "Back",
            },
            onClick: closeDetails,
        },
        {
            key: "Score",
            name: AddButton(),
            iconProps: {
                iconName: "6PointStar",
            },
            onClick: onshowScorePanel,
        },
    ];

    const getImage = (item: IMahJongSet): JSX.Element[] | JSX.Element => {
        return map(
            imageSet(item),
            (imageSrc, index): JSX.Element => {
                return <Image key={index} src={imageSrc} alt={displayTileType(item.tileType)} height={55} />;
            }
        );
    };

    const onRenderCell = (item?: IMahJongSet): JSX.Element | null => {
        if (!item) {
            return null;
        }

        return (
            <div className={classNames.listItem}>
                {getImage(item)}
                <div className={classNames.listDescription}>
                    <div>{displayTileType(item?.tileType)}</div>
                    <div>{displaySetType(item?.setType)}</div>
                </div>
                <IconButton
                    iconProps={{ iconName: "Delete" }}
                    title={DeleteButton()}
                    ariaLabel={DeleteButton()}
                    onClick={() => {
                        removeSetClicked(item);
                    }}
                    styles={styles.listButton}
                />
            </div>
        );
    };

    return (
        <div className={classNames.page}>
            <CommandBar items={items} styles={styles.commandBar} />
            <div className={classNames.content}>
                <h1>{user.name}</h1>
                <p>{CurrentScore(userScore?.points ?? 0)}</p>
                <Stack tokens={{ childrenGap: 10 }}>
                    <Toggle
                        label={WinnerToggleLabel()}
                        checked={userScore?.isWinner}
                        inlineLabel
                        onText={OnToggle()}
                        offText={OffToggle()}
                        onChange={onChangeWinner}
                    />
                    <Toggle
                        label={EastToggleLabel()}
                        checked={userScore?.isEast}
                        inlineLabel
                        onText={OnToggle()}
                        offText={OffToggle()}
                        onChange={onChangeEast}
                    />
                </Stack>
                <List items={userScore?.sets} onRenderCell={onRenderCell} />
                <PrimaryButton styles={styles.footer} onClick={onshowScorePanel}>
                    {AddButton()}
                </PrimaryButton>
            </div>
            <ScorePanel isOpen={showScorePanel} onClose={closeScorePanel} onCancel={cancelScorePanel} />
        </div>
    );
}
