import React, { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { selectPage, Page as PageType, load, selectLanguage } from "./MainSlice";
import { load as loadMahjong } from "../mahjong/overview/MahjongOverviewSlice";
import { load as loadTraining } from "../training/overview/TrainingOverviewSlice";
import { load as loadPlayground } from "../playground/overview/PlaygroundOverviewSlice";
import { MahjongOverview } from "../mahjong/overview/MahjongOverview";
import { PlaygroundOverview } from "../playground/overview/PlaygroundOverview";
import { TrainingOverview } from "../training/overview/TrainingOverview";
import { MainContent } from "./MainContent";

export function MainPage() {
    const page = useSelector(selectPage);
    const language = useSelector(selectLanguage);
    const dispatch = useDispatch();
    useEffect((): void => {
        dispatch(load());
        dispatch(loadMahjong());
        dispatch(loadTraining());
        dispatch(loadPlayground());
    }, [dispatch]);

    return (
        <div>
            {page === PageType.Main && <MainContent />}
            {page === PageType.MahJong && <MahjongOverview />}
            {page === PageType.Training && <TrainingOverview />}
            {page === PageType.Playground && <PlaygroundOverview />}
        </div>
    );
}
