import {
    ICommandBarItemProps,
    classNamesFunction,
    CompoundButton,
    Persona,
    PersonaSize,
    List,
    FontIcon,
    PrimaryButton,
} from "office-ui-fabric-react";
import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";

import { mahjongOverviewClassNames, getMahjongOverviewStyles, IMahjongOverviewClassNames } from "./MahjongOverview.styles";
import {
    EditPlayersButton,
    CurrentScore,
    GameScore,
    MahJongHeader,
    ClearButton,
    ClearAllButton,
    ClearGameButton,
    AddButton,
    SaveScoreButton,
    ClearAllGamesButton,
    FullGameScore,
} from "../../../localization/DisplayStrings";
import {
    load,
    selectPlayers,
    selectCurrentPlayers,
    setPlayers,
    addUser,
    deleteUser,
    selectCurrentGame,
    clearAll,
    clearGame,
    saveGame,
    clearAllGames,
    selectGames,
    history,
    IMahJongHistory,
} from "./MahjongOverviewSlice";
import { IMahJongUser, getScore } from "../Mahjong.model";
import { SelectUserPanel } from "../userPanel/SelectUserPanel";
import { MahjongDetails } from "../details/MahjongDetails";
import { AddUserPanel } from "../addUserPanel/AddUserPanel";
import { find } from "lodash";
import { Page } from "../../../components/page/Page";

export function MahjongOverview(): JSX.Element {
    const classNames = classNamesFunction<{}, IMahjongOverviewClassNames>()(mahjongOverviewClassNames);
    const styles = getMahjongOverviewStyles();

    const [refreshCount, setRefreshCount] = React.useState(0);
    const [isPlayerAddOpen, setIsPlayerAddOpen] = React.useState(false);
    const [isPlayerSelectOpen, setIsPlayerSelectOpen] = React.useState(false);
    const [currentPlayer, setCurrentPlayer] = React.useState<IMahJongUser | undefined>(undefined);
    const [historyGames, setHistoryGames] = React.useState<IMahJongHistory[]>([]);

    const allPlayers = useSelector(selectPlayers);
    const currentPlayers = useSelector(selectCurrentPlayers);
    const players = useSelector(selectCurrentPlayers);
    const game = useSelector(selectCurrentGame);
    const games = useSelector(selectGames);

    const dispatch = useDispatch();
    useEffect((): void => {
        const hist = history(games, currentPlayers);
        setHistoryGames(hist);
        setRefreshCount(refreshCount + 1);
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [games, currentPlayers]);

    const onSaveSelectedPlayers = (selectedPlayers: IMahJongUser[]): void => {
        dispatch(setPlayers(selectedPlayers));
        setIsPlayerSelectOpen(false);
    };

    const onAddPlayer = (player: IMahJongUser): void => {
        dispatch(addUser(player));
        setIsPlayerAddOpen(false);
    };
    const onCancel = (): void => {
        setIsPlayerAddOpen(false);
        setIsPlayerSelectOpen(false);
    };

    const onDeletePlayer = (player: IMahJongUser): void => {
        dispatch(deleteUser(player));
    };
    const onOpenSelectedPlayers = () => setIsPlayerSelectOpen(true);
    const onOpenAddPlayers = () => setIsPlayerAddOpen(true);

    const onClearAll = (): void => {
        dispatch(clearAll());
        setRefreshCount(refreshCount + 1);
    };
    const onClearGame = (): void => {
        dispatch(clearGame());
        setRefreshCount(refreshCount + 1);
    };
    const onClearAllGames = (): void => {
        dispatch(clearAllGames());
        setRefreshCount(refreshCount + 1);
    };
    const onNavigateBackFromDetails = (): void => {
        setCurrentPlayer(undefined);
        setRefreshCount(refreshCount + 1);
    };

    const openPlayer = (item?: IMahJongUser) => () => {
        setCurrentPlayer(item);
    };

    const items: ICommandBarItemProps[] = [
        {
            key: "Select",
            iconProps: {
                iconName: "People",
            },
            ariaLabel: EditPlayersButton(),
            subMenuProps: {
                items: [
                    {
                        key: "add",
                        text: AddButton(),
                        iconProps: { iconName: "People" },
                        onClick: onOpenAddPlayers,
                    },
                    {
                        key: "SelectOrDelete",
                        text: EditPlayersButton(),
                        iconProps: { iconName: "People" },
                        onClick: onOpenSelectedPlayers,
                    },
                ],
            },
        },
        {
            key: "save",
            text: SaveScoreButton(),
            iconProps: {
                iconName: "Save",
            },
            onClick: () => {
                dispatch(saveGame());
            },
        },
        {
            key: "Clear",
            iconProps: {
                iconName: "Delete",
            },
            ariaLabel: ClearButton(),
            subMenuProps: {
                items: [
                    {
                        key: "ClearAll",
                        text: ClearAllButton(),
                        iconProps: { iconName: "UserRemove" },
                        onClick: onClearAll,
                        disabled: true,
                    },
                    {
                        key: "ClearGames",
                        text: ClearAllGamesButton(),
                        iconProps: { iconName: "RemoveFromShoppingList" },
                        onClick: onClearAllGames,
                    },
                    {
                        key: "ClearGame",
                        text: ClearGameButton(),
                        iconProps: { iconName: "RemoveFromShoppingList" },
                        onClick: onClearGame,
                    },
                ],
            },
        },
    ];

    const onRenderCell = (item?: IMahJongUser): JSX.Element | null => {
        if (!item) {
            return null;
        }

        const currentScore = getScore(game, item);
        const winner = currentScore.isWinner ? (
            <FontIcon iconName="SkypeCheck" className={currentScore.isEast ? classNames.winnerIcon2 : classNames.winnerIcon} />
        ) : null;
        const east = currentScore.isEast ? <FontIcon iconName="CompassNW" className={classNames.eastIcon} /> : null;

        const summarizedGameScore = find(historyGames, (hist) => hist.playerId === item.id);
        const scoreText = summarizedGameScore ? FullGameScore(currentScore.gameScore, summarizedGameScore.score) : GameScore(currentScore.gameScore);
        return (
            <CompoundButton styles={styles.listItem} onClick={openPlayer(item)}>
                <Persona text={item?.name} secondaryText={CurrentScore(currentScore.points)} tertiaryText={scoreText} size={PersonaSize.size72} />
                {east}
                {winner}
            </CompoundButton>
        );
    };

    if (currentPlayer) {
        return <MahjongDetails user={currentPlayer} onNavigateBack={onNavigateBackFromDetails} />;
    }

    return (
        <Page commandBarItems={items} header={MahJongHeader()}>
            <AddUserPanel isOpen={isPlayerAddOpen} onAdd={onAddPlayer} onCancel={onCancel} allPlayers={allPlayers} />
            <SelectUserPanel
                isOpen={isPlayerSelectOpen}
                onSave={onSaveSelectedPlayers}
                onDelete={onDeletePlayer}
                allPlayers={allPlayers}
                currentPlayers={currentPlayers}
            />
            <List className={classNames.list} version={refreshCount} items={players} onRenderCell={onRenderCell} />
            {players?.length < 4 && (
                <PrimaryButton styles={styles.footerButton} onClick={onOpenAddPlayers}>
                    {EditPlayersButton()}
                </PrimaryButton>
            )}
        </Page>
    );
}
