// type supportedLanguages = "da" | "en";

const supportedRegions: { [key: string]: string } = { da: "da-DK", en: "en-US", ru: "ru-RU", zh: "zh-TW" };
const supportedLanguages: { [key: string]: string } = { da: "da", en: "en", ru: "ru", zh: "zh" };

let locale: string = "da";
export function setLocale(value: string) {
    locale = value;
}

export function getLocale(): string {
    return supportedLanguages[locale];
}

function getRegion(): string {
    return supportedRegions[getLocale()];
}

export function t<T>(loc: { [key: string]: T }) {
    const locale = getLocale();
    if (locale && loc && loc[locale]) {
        return loc[locale];
    }

    return loc["da"] || loc["en"];
}

export function dateToShortString(date: Date) {
    return date.toLocaleDateString(getRegion(), { month: "numeric", day: "numeric" });
}

export function dateToString(date: Date) {
    return date.toLocaleDateString(getRegion());
}

export function dateToMonthString(date: Date) {
    return date.toLocaleDateString(getRegion(), { year: "numeric", month: "long" });
}
