import { ICommandBarStyles, IStackStyles, memoizeFunction, mergeStyleSets, IButtonStyles } from "office-ui-fabric-react";

export interface IPageStyles {
    commandBar: ICommandBarStyles;
}

export const getPageStyles = (): IPageStyles => ({
    commandBar: {
        root: {
            padding: "5px",
        },
    },
});

export interface IPageClassNames {
    page: string;
    commandBar: string;
    content: string;
}

export const pageClassNames = memoizeFunction(
    (): IPageClassNames => {
        return mergeStyleSets({
            page: {
                overflow: "auto",
            },
            commandBar: {
                height: "44px",
            },
            content: {
                margin: "0 10px",
            },
        });
    }
);
