import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { RootState, AppThunk } from "../../app/store";
import { saveData, isSupported, loadData } from "../../utils/StorageUtil";
import { setLocale } from "../../utils/LocalizationUtil";

export enum Page {
    Main,
    MahJong,
    Training,
    Playground,
}

interface MainState {
    page: Page;
    language: string;
}

const initialState: MainState = {
    page: Page.MahJong,
    language: "da",
};

export const mainSlice = createSlice({
    name: "main",
    initialState,
    reducers: {
        navigate: (state, action: PayloadAction<Page>) => {
            state.page = action.payload;
            saveState(state);
        },
        setLanguage: (state, action: PayloadAction<string>) => {
            setLocale(action.payload);
            state.language = action.payload;
            saveState(state);
        },
        loadFailed: (state) => {
            setLocale(initialState.language);
            state = initialState;
        },
        loadSuccess: (state, action: PayloadAction<MainState>) => {
            state.page = action.payload?.page ?? Page.MahJong;
        },
    },
});

const { loadSuccess, loadFailed } = mainSlice.actions;
export const { navigate, setLanguage } = mainSlice.actions;

const storageName = "Main";

function saveState(state: MainState) {
    saveData(storageName, state);
}

export const load = (): AppThunk => (dispatch) => {
    if (isSupported) {
        dispatch(loadSuccess(loadData(storageName) as MainState));
    } else {
        dispatch(loadFailed());
    }
};

export const selectPage = (state: RootState) => state.main.page;
export const selectLanguage = (state: RootState) => state.main.language;

export default mainSlice.reducer;
