import React, { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { navigate, selectPage, Page as PageType, load, setLanguage } from "./MainSlice";
import { Nav, ICommandBarItemProps } from "office-ui-fabric-react";
import { HomeTitle, MahJongTitle, TrainingTitle, PlaygroundTitle } from "../../localization/DisplayStrings";
import { Page } from "../../components/page/Page";

export function MainContent() {
    const [refreshCount, setRefreshCount] = React.useState(0);
    const dispatch = useDispatch();
    const items: ICommandBarItemProps[] = [
        {
            key: "Language",
            iconProps: {
                iconName: "Globe",
            },
            subMenuProps: {
                items: [
                    {
                        key: "da",
                        text: "Dansk",
                        onClick: (ev?: React.MouseEvent<HTMLElement> | React.KeyboardEvent<HTMLElement>) => {
                            dispatch(setLanguage("da"));
                            setRefreshCount(refreshCount + 1);
                        },
                    },
                    {
                        key: "en",
                        text: "English",
                        onClick: (ev?: React.MouseEvent<HTMLElement> | React.KeyboardEvent<HTMLElement>) => {
                            dispatch(setLanguage("en"));
                            setRefreshCount(refreshCount + 1);
                        },
                    },
                    {
                        key: "ru",
                        text: "",
                        onClick: (ev?: React.MouseEvent<HTMLElement> | React.KeyboardEvent<HTMLElement>) => {
                            dispatch(setLanguage("ru"));
                            setRefreshCount(refreshCount + 1);
                        },
                    },
                    {
                        key: "zh",
                        text: "()",
                        onClick: (ev?: React.MouseEvent<HTMLElement> | React.KeyboardEvent<HTMLElement>) => {
                            dispatch(setLanguage("zh"));
                            setRefreshCount(refreshCount + 1);
                        },
                    },
                ],
            },
        },
    ];

    return (
        <Page commandBarItems={items} header={HomeTitle()}>
            <Nav
                selectedKey="key0"
                groups={[
                    {
                        links: [
                            {
                                name: MahJongTitle(),
                                url: "",
                                key: "key0",
                                target: "_blank",
                                onClick: () => {
                                    dispatch(navigate(PageType.MahJong));
                                },
                            },
                            {
                                name: TrainingTitle(),
                                url: "",
                                key: "key1",
                                target: "_blank",
                                onClick: () => {
                                    dispatch(navigate(PageType.Training));
                                },
                            },
                            {
                                name: PlaygroundTitle(),
                                url: "",
                                key: "key2",
                                target: "_blank",
                                onClick: () => {
                                    dispatch(navigate(PageType.Playground));
                                },
                            },
                        ],
                    },
                ]}
            />
        </Page>
    );
}
