import React, { useEffect } from "react";
import { MainPage } from "./pages/main/Main";
import { initializeIcons, loadTheme } from "office-ui-fabric-react";
import { store } from "./app/store";
import { Provider } from "react-redux";
import { ThemeProvider } from "office-ui-fabric-react/lib/Foundation";

const myTheme = loadTheme({
    palette: {
        themePrimary: "#2266e3",
        themeLighterAlt: "#f5f8fe",
        themeLighter: "#d8e4fb",
        themeLight: "#b8cef7",
        themeTertiary: "#759fee",
        themeSecondary: "#3a76e6",
        themeDarkAlt: "#1f5bcc",
        themeDark: "#1a4dac",
        themeDarker: "#13397f",
        neutralLighterAlt: "#faf9f8",
        neutralLighter: "#f3f2f1",
        neutralLight: "#edebe9",
        neutralQuaternaryAlt: "#e1dfdd",
        neutralQuaternary: "#d0d0d0",
        neutralTertiaryAlt: "#c8c6c4",
        neutralTertiary: "#a19f9d",
        neutralSecondary: "#605e5c",
        neutralPrimaryAlt: "#3b3a39",
        neutralPrimary: "#323130",
        neutralDark: "#201f1e",
        black: "#000000",
        white: "#ffffff",
    },
});

const App = (): JSX.Element => {
    useEffect(() => {
        initializeIcons();
        // initializeTheme();
    });

    return (
        <React.StrictMode>
            <Provider store={store}>
                <ThemeProvider theme={myTheme}>
                    <MainPage />
                </ThemeProvider>
            </Provider>
        </React.StrictMode>
    );
};

// function initializeTheme(): void {
//   if (!environment.production && !window[GlobalObjects.theme]) {
//       window[GlobalObjects.theme] = myTheme;
//   }
// }

export default App;
