// import uuid from "uuid";
import { v4 as uuidv4 } from "uuid";
import { find } from "lodash";

export interface IMahJongUser {
  id: string;
  name: string;
}

export interface IMahJongScore {
  gameScore: number;
  points: number;
  sets: IMahJongSet[];
  isEast?: boolean;
  isWinner?: boolean;
  playerId: string;
}

export enum MahJongTileTypes {
  Dragon,
  Wind,
  Bamboo,
  Circle,
  Character,
  Flower,
}

export enum MahJongSetTypes {
  Chow,
  Poong,
  Kong,
  Pair,
  Single,
}

export interface IMahJongSet {
  tileType: MahJongTileTypes;
  setType: MahJongSetTypes;
  inHand?: boolean;
  oneOrNine?: boolean;
  ownWind?: boolean;
}

export interface IMahJongGame {
  id: string;
  score: IMahJongScore[];
}

export function createUser(name: string): IMahJongUser {
  return { id: uuidv4(), name };
}

export function createGame(): IMahJongGame {
  return { id: uuidv4(), score: [] };
}

export function createScore(playerId: string): IMahJongScore {
  return {
    gameScore: 0,
    points: 0,
    sets: [],
    playerId,
  };
}

export function getScore(
  game: IMahJongGame,
  player: IMahJongUser
): IMahJongScore {
  return getScoreById(game, player.id) || createScore(player.id);
}

function getScoreById(
  game: IMahJongGame,
  id?: string
): IMahJongScore | undefined {
  return find(
    game.score,
    (score: IMahJongScore): boolean => score.playerId === id
  );
}
