import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { AppThunk, RootState } from "../../../app/store";
import { isSupported, loadData, saveData } from "../../../utils/StorageUtil";
import {
  map,
  find,
  uniqBy,
  filter,
  without,
  forEach,
  sumBy,
  some,
  min,
} from "lodash";

interface IBuildingBlock {
  name: string;
  width: number;
  length: number;
  needRoad?: boolean;
  location?: IGraphPoint;
}

interface IRoadBlock {
  name: string;
  width: number;
  length: number;
  location: IGraphPoint;
}

interface IGraphPoint {
  x: number;
  y: number;
}

interface IArea {
  graph: IGraphPoint[];
}

interface PlaygroundOverviewState {
  area: IArea;
  townHall: IBuildingBlock;
  buildings: IBuildingBlock[];
}

const initialState: PlaygroundOverviewState = {
  area: { graph: [] },
  townHall: createBuilding("TownHall", 7, 6, true),
  buildings: [{ name: "TH", width: 4, length: 4, needRoad: true }],
};

export const overviewSlice = createSlice({
  name: "PlaygroundOverview",
  initialState,
  reducers: {
    loadFailed: (state) => {
      state = initialState;
    },
    loadSuccess: (state, action: PayloadAction<PlaygroundOverviewState>) => {},
  },
});

const { loadFailed, loadSuccess } = overviewSlice.actions;
export const {
  //
} = overviewSlice.actions;

const storageName = "Play";

function saveState(state: PlaygroundOverviewState) {
  saveData(storageName, state);
}

export const load = (): AppThunk => (dispatch) => {
  if (isSupported) {
    dispatch(loadSuccess(loadData(storageName) as PlaygroundOverviewState));
  } else {
    dispatch(loadFailed());
  }
};

function createBuilding(
  name: string,
  width: number,
  length: number,
  needRoad: boolean = true
) {
  return { name, width, length, needRoad };
}

function createDuplicateBuildings(
  building: IBuildingBlock,
  count: number
): IBuildingBlock[] {
  const list = [];
  for (let i = 0; i < count; i++) {
    list.push({ ...building });
  }
  return list;
}

// 0,0
