import { ICommandBarStyles, IStackStyles, memoizeFunction, mergeStyleSets, IButtonStyles } from "office-ui-fabric-react";

export interface ITrainingOverviewStyles {
    dashboard: IStackStyles;
    listItem: Partial<IButtonStyles>;
    footerButton: Partial<IButtonStyles>;
    listButton: Partial<IButtonStyles>;
}

export const getTrainingOverviewStyles = (): ITrainingOverviewStyles => ({
    dashboard: {
        root: { padding: "24px 0 0 0" },
    },
    listItem: {
        root: {
            maxWidth: "100%",
            width: "100%",
            padding: "20px 10px 20px 20px",
            margin: "10px 0",
        },
        flexContainer: {
            justifyContent: "flex-start",
        },
    },
    footerButton: {
        root: {
            marginTop: 30,
            float: "right",
            marginRight: 30,
        },
    },
    listButton: {
        root: {
            marginLeft: "auto",
        },
    },
});

export interface ITrainingOverviewClassNames {
    list: string;
    listItem: string;
    listText: string;
}

export const trainingOverviewClassNames = memoizeFunction(
    (): ITrainingOverviewClassNames => {
        return mergeStyleSets({
            list: {
                margin: "0 10px",
            },
            listItem: {
                display: "flex",
            },
            listText: {
                margin: "6px",
            },
        });
    }
);
